import React, { useState, useRef } from 'react';
import toast from 'react-hot-toast';
import styles from '../UAG.module.css';

interface MaiCardProps {
  title?: string;
  subTitle?: string;
  price?: string;
  pricePerPing?: string;
  stats?: { label: string; value: string }[];
  imageUrl?: string;
}

export default function MaiCard({
  title: initialTitle = " 12F322",
  subTitle: initialSubTitle = "  67.3    5 ",
  price: initialPrice = "NT$ 32,880,000",
  pricePerPing: initialPricePerPing = "52.1 /",
  stats: initialStats = [
    { label: "", value: "-2% ~ -6%" },
    { label: "", value: " 8.6 " },
    { label: "", value: "52.8 / 51.5 / 50.9" }
  ],
  imageUrl: initialImageUrl = "data:image/svg+xml;utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' width='320' height='200'%3E%3Crect width='100%25' height='100%25' fill='%23e5edff'/%3E%3Ctext x='50%25' y='50%25' text-anchor='middle' fill='%231749d7'%3EMAIN%3C/text%3E%3C/svg%3E"
}: MaiCardProps) {
  
  const [title, setTitle] = useState(initialTitle);
  const [subTitle, setSubTitle] = useState(initialSubTitle);
  const [price, setPrice] = useState(initialPrice);
  const [pricePerPing, setPricePerPing] = useState(initialPricePerPing);
  const [stats, setStats] = useState(initialStats);
  const [imageUrl, setImageUrl] = useState(initialImageUrl);
  
  const fileInputRef = useRef<HTMLInputElement>(null);

  const handleGenerateReport = () => {
    toast.success('...');
  };

  const handleImportClick = () => {
    fileInputRef.current?.click();
  };

  const handleFileChange = async (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (!file) return;

    const toastId = toast.loading('...');

    try {
      const reader = new FileReader();
      reader.onloadend = async () => {
        const base64data = reader.result as string;
        setImageUrl(base64data); // Show the uploaded image

        //  API  ( /api/x-raymike)
        // const res = await fetch('/api/x-raymike', { method: 'POST', body: JSON.stringify({ image: base64data }) });
        
        setTimeout(() => {
          // 
          setTitle(" 18F");
          setSubTitle("  120.5    10 ");
          setPrice("NT$ 188,000,000");
          setPricePerPing("156.0 /");
          setStats([
             { label: "", value: "-5% ~ -8%" },
             { label: "", value: " 45.2 " },
             { label: "", value: "155.0 / 152.3 / 148.9" }
          ]);
          toast.success('', { id: toastId });
        }, 1500);
      };
      reader.readAsDataURL(file);
    } catch (error) {
      toast.error('', { id: toastId });
    }
  };

  return (
    <section className={`${styles['uag-card']} ${styles['k-span-3']}`}>
      <div className={styles['uag-card-header']}>
        <div><div className={styles['uag-card-title']}></div><div className={styles['uag-card-sub']}></div></div>
        <div className={styles['uag-actions']}>
          <input type="file" ref={fileInputRef} style={{ display: 'none' }} accept="image/*" onChange={handleFileChange} />
          <button className={`${styles['uag-btn']} ${styles['secondary']}`} onClick={handleImportClick} style={{ marginRight: '8px' }}></button>
          <button className={`${styles['uag-btn']} ${styles['primary']}`} onClick={handleGenerateReport}></button>
        </div>
      </div>
      <div className={styles['card-body']}>
        <section aria-label="" className={styles['mai-card']}>
          <header className={styles['mai-head']}>
            <span className={styles['mai-tag']}></span>
            <h3 className={styles['mai-title']}>{title}</h3>
            <p className={styles['mai-sub']}>{subTitle}</p>
          </header>
          <div className={styles['mai-body']}>
            <div className={styles['mai-left']}>
              <div className={styles['mai-price']}>
                <div className={styles['mai-price-label']}></div><div className={styles['mai-price-value']}>{price}</div><div className={styles['mai-price-unit']}>{pricePerPing}</div>
              </div>
              <ul className={styles['mai-stats']}>
                {stats.map((stat, index) => (
                  <li key={index}><span>{stat.label}</span><b>{stat.value}</b></li>
                ))}
              </ul>
            </div>
            <div className={styles['mai-right']}>
              <div className={styles['mai-thumb']}><img alt="" src={imageUrl} style={{ width: '100%', height: '100%', objectFit: 'cover' }} /></div>
              <div className={styles['mai-mini']}>
                <div style={{ background: '#f0f4ff', borderRadius: '8px', aspectRatio: '3/2' }}></div>
                <div style={{ background: '#f0f4ff', borderRadius: '8px', aspectRatio: '3/2' }}></div>
                <div style={{ background: '#f0f4ff', borderRadius: '8px', aspectRatio: '3/2' }}></div>
              </div>
            </div>
          </div>
          <div className={styles['mai-actions']}>
            <button className={`${styles['mai-btn']} ${styles['primary']}`}></button>
            <button className={`${styles['mai-btn']} ${styles['line']}`}>LINE </button>
          </div>
        </section>
      </div>
    </section>
  );
}
